<template>
  <h2 :id="title" class="category">
    <span>{{ title }}</span>
    <button>
      <img class="close" src="@/assets/img/dropArrow.svg" alt="dropArrow">
    </button>
  </h2>
</template>

<script>
import {mapMutations} from "vuex";

export default {
  name: "CategoryTitle",
  props: {
    title: String
  },
  methods: {
    ...mapMutations({
      setCategoryOffset: 'main/setCategoryOffset'
    })
  },
  mounted() {
    this.setCategoryOffset(document.querySelector('#' + this.title).offsetTop)
  }
}
</script>

<style scoped>
h2 {
  width: 100%;
  background: #FFAB6F;
  color: #ffffff;
  font-size: 4.5vw;
  display: flex;
  justify-content: space-between;
  box-shadow: #00000020 0 2px 4px;
  padding: 2vw 3vw 2vw 6vw;
}

.close {
  transform: rotate(180deg);
}
</style>