<template>
  <div class="item" @click="openPopup">
    <img :src="require('@/assets/' + detail.image)" alt="item image">
    <div class="title">{{ detail.title }}</div>
    <div class="price">{{ detail.price }}</div>
  </div>
</template>

<script>
import {mapMutations} from "vuex";

export default {
  name: "CategoryItem",
  data () {
    return {
      detail: {...this._detail}
    }
  },
  props: {
    _detail: Object
  },
  methods: {
    ...mapMutations({
      setPopupData: "main/setPopupData",
      setIsOpen: "main/setIsOpen"
    }),
    openPopup() {
      this.setPopupData({...this.detail})
      this.setIsOpen(true)
    }
  }
}
</script>

<style scoped>
.item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2vw 0;
  border-radius: 8px;
  background: #ffffff;
  box-shadow: #00000020 2px 2px 4px;
}

img {
  width: 50%;
  margin-bottom: 10px;
}

.title {
  margin-bottom: 5px;
  font-size: 3vw;
}

.price {
  font-size: 2.5vw;
}
</style>