<template>
  <div class="popup_container">
    <div class="inner">
      <img class="close" src="@/assets/img/closeBtn.svg" alt="close button" @click="closePopup">
      <img class="item-img" :src="require('@/assets/' + popupData.image)" alt="item image">
      <h3 class="item-name">
        <span>{{popupData.title}}</span>
        <div>
          <span>수량</span>
          <div class="inp-wrap">
            <button @click="downCnt"><img src="@/assets/img/minusBtn.svg" alt="minusBtn"></button>
            <input type="number" :value="popupData.count">
            <button @click="upCnt"><img src="@/assets/img/plusBtn.svg" alt="plusBtn"></button>
          </div>
        </div>
      </h3>

      <ul class="option-list">
        <li class="option" v-for="item in popupData.option">

          <div class="temperature" v-if="item.type === 'temperature'">
            <button @click="setTemperature('hot')" :class="temperature.hot">{{ item.name[0] }}</button>
            <button @click="setTemperature('ice')" :class="temperature.ice">{{ item.name[1] }}</button>
          </div>

          <div class="count" v-else-if="item.type === 'count'">
            <span>{{item.name}}</span>
            <div class="inp-wrap">
              <button @click="decrease(item)"><img src="@/assets/img/minusBtn.svg" alt="minusBtn"></button>
              <input type="number" :value="item.value" :min="item.min" :max="item.max">
              <button @click="increase(item)"><img src="@/assets/img/plusBtn.svg" alt="plusBtn"></button>
            </div>
          </div>

          <div class="button" v-else-if="item.type === 'button'">
            <span>{{item.name}}</span>
            <button @click="addSyrup(item)" v-if="item.value === 0">추가</button>
            <button @click="delSyrup(item)" :style="{background: '#c7c7c7'}" v-if="item.value === 1">취소</button>
          </div>

        </li>
      </ul>
      <div class="price">
        <span>금액</span>
        <span>{{ popupData.totalPrice }} 원</span>
      </div>
      <div class="btn-wrap">
        <button @click="addCart">장바구니</button>
        <button @click="goToCart">바로 구매</button>
      </div>
    </div>
  </div>
</template>

<script>
import {mapMutations} from "vuex";

export default {
  name: "SelectOption",
  data() {
    return {
      popupData: {},
      temperature: {
        hot: "choose",
        ice: ""
      },
      originData: {}
    }
  },
  created() {
    this.popupData = JSON.parse(JSON.stringify(this.$store.state.main.popupData))
    this.originData = JSON.parse(JSON.stringify(this.$store.state.main.popupData))
    this.totalPrice = this.popupData.price
  },
  methods: {
    ...mapMutations({
      setIsOpen: "main/setIsOpen",
      setHasCart: "main/setHasCart"
    }),
    closePopup() {
      this.popupData = this.originData
      this.setIsOpen(false)
    },
    decrease(item) {
      if(item.value > item.min) {
        item.value--
        this.popupData.totalPrice -= item.price
      }
    },
    increase(item) {
      if(item.value < item.max) {
        item.value++
        this.popupData.totalPrice += item.price
      }
    },
    addCart() {
      const obj = {
        idx: this.$store.state.main.hasCart,
        title: this.popupData.title,
        price: this.popupData.totalPrice,
        temperature: this.temperature.hot === '' ? 'ice' : 'hot',
        count: this.popupData.count,
        option: []
      }

      this.popupData.option.forEach(item => {
        obj.option.push({...item})
      })

      if(sessionStorage.getItem("cart") === null) {
        sessionStorage.setItem("cart", JSON.stringify([obj]))
      } else {
        const temp = JSON.parse(sessionStorage.getItem("cart"))
        temp.push(obj)
        sessionStorage.setItem("cart", JSON.stringify(temp))
      }

      this.setHasCart(JSON.parse(sessionStorage.getItem("cart")).length)
      this.popupData = this.originData
      this.setIsOpen(false)
    },
    setTemperature(temper) {
      switch(temper) {
        case 'hot':
          this.temperature = {hot: "choose", ice: ""}
          break;
        case 'ice':
          this.temperature = {hot: "", ice: "choose"}
          break;
      }
    },
    addSyrup(item) {
      item.value = 1
      this.popupData.totalPrice += item.price
    },
    delSyrup(item) {
      item.value = 0
      this.popupData.totalPrice -= item.price
    },
    goToCart() {
      this.addCart()
      this.$router.push('/cart')
    },
    downCnt() {
      if(this.popupData.count > 0) {
        this.popupData.count--
        this.popupData.totalPrice -= this.popupData.price
      }
    },
    upCnt() {
      this.popupData.count++
      this.popupData.totalPrice += this.popupData.price
    }
  }
}
</script>

