<template>
  <div>
    <Header/>
    <Navigation position="nav"/>
    <Category
        v-for="item in this.$store.state.main.itemArr"
        :item="item"/>
    <SelectOption
        v-if="this.$store.state.main.isOpen"/>
    <GoToCart
        v-if="this.$store.state.main.hasCart > 0"/>
    <Footer/>
  </div>
</template>

<script>
import Header from "@/components/layout/Header";
import Category from "@/components/layout/Category";
import SelectOption from "@/components/template/popup/SelectOption";
import {mapActions, mapMutations} from "vuex";
import GoToCart from "@/components/template/popup/GoToCart";
import Navigation from "@/components/template/head/Navigation";
import Footer from "@/components/layout/Footer";

export default {
  name: "MainContainer",
  components: {Footer, GoToCart, Category, Header, SelectOption, Navigation},
  data() {
    return {
    }
  },
  methods: {
    ...mapMutations({
      setHasCart: 'main/setHasCart',
      setCategoryOffset: 'main/setCategoryOffset',
      changeNav: 'main/changeNav'
    }),
    ...mapActions({
      getItemArr: 'main/getItemArr',
    }),
    setNavState(offset) {
      const temp = []
      document.querySelectorAll('.category').forEach(item => {
        temp.push(item.offsetTop)
      })
      this.setCategoryOffset(temp)
      const arr = Array.from(this.$store.state.main.categoryOffset)

      for(let i = 0; i < arr.length; i++) {
        if(offset <= arr[0]
           || (offset >= arr[i] && offset < arr[i + 1])
        ) {
          this.changeNav(this.$store.state.main.itemArr[i].category)
          break;
        } else if(i === arr.length - 1) {
          this.changeNav(this.$store.state.main.itemArr[i].category)
          break;
        }
      }
    }
  },
  created() {
    this.getItemArr()
    const cart =
        JSON.parse(sessionStorage.getItem('cart')) === null
        ? 0
        : JSON.parse(sessionStorage.getItem('cart')).length
    this.setHasCart(cart)

    window.addEventListener('scroll', () => {
      this.setNavState(window.scrollY)
    })
  },
}
</script>

