<template>
  <nav v-if="position === 'nav'">
    <ul>
      <li
          v-for="item in this.$store.state.main.itemArr"
          :class="item.class"
          @click="changeNav(item)">
        <a :href="`#${item.category}`">{{item.category}}</a>
      </li>
    </ul>
  </nav>
  <div class="box" v-else-if="position === 'cart'">
    <span>장바구니</span>
    <button @click="prevBtn"><img src="@/assets/img/backArrow.svg" alt="backBtn"></button>
  </div>
</template>

<script>
import {mapMutations} from "vuex";

export default {
  name: "Navigation",
  props: {
    position: String
  },
  methods: {
    ...mapMutations({
      changeNavStore: 'main/changeNav'
    }),
    changeNav(item) {
      this.changeNavStore(item.category)
    },
    prevBtn() {
      this.$router.back()
    }
  }
}
</script>

