<template>
  <h1>
    <span>{{ storeName }}</span>
    <button @click="goLogin">로그인</button>
  </h1>
</template>

<script>
export default {
  name: "Title",
  data() {
    return {
      storeName: "coffee"
    }
  },
  methods: {
    goLogin() {
      this.$router.push('/login')
    }
  },
  created() {
    if (this.$route.params.id > 0) {
      this.storeName = "커피공장"
    }
  }
}
</script>

